import React, { useState } from "react";
import { Button } from "@material-ui/core";
import { TextField } from "@material-ui/core";
import { Dialog } from "@material-ui/core";
import { DialogActions } from "@material-ui/core";
import { DialogContent } from "@material-ui/core";
import { DialogContentText } from "@material-ui/core";
import { DialogTitle } from "@material-ui/core";

function AddForm() {
  const [open, setOpen] = useState(false);
  const [color] = React.useState("primary");

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const [value, setValue] = React.useState(null);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <Button color={color} variant="outlined" onClick={handleClickOpen}>
        ADD
      </Button>
      <Dialog open={open} onClose={handleClose} maxWidth="md">
        <DialogTitle>ADD</DialogTitle>
        <DialogContent>
          <TextField label="Business Code" variant="outlined" />
          <TextField
            label="Customer Number"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Clear Date"
            type={"date"}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Business Year"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
          <TextField label="Document Id" variant="outlined" />
          <TextField
            label="Posting Date"
            type={"date"}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Document Create Date"
            type={"date"}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Due Date"
            type={"date"}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
            style={{ marginLeft: ".5rem" }}
          />
          <TextField label="Invoice Currency" variant="outlined" />
          <TextField
            label="Document Type"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Posting Id"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
          <TextField label="Total Open Amount" variant="outlined" />
          <TextField
            label="Baseline Create Date"
            type={"date"}
            variant="outlined"
            InputLabelProps={{ shrink: true }}
          />
          <TextField
            label="Customer Payment Terms"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
          <TextField
            label="Invoice Id"
            variant="outlined"
            style={{ marginLeft: ".5rem" }}
          />
        </DialogContent>
        <DialogActions>
          <Button variant="outlined" onClick={handleClose} fullWidth>
            ADD
          </Button>
          <Button variant="outlined" onClick={handleClose} fullWidth>
            CANCEL
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default AddForm;
